.app_wrapper{
  margin: 0 auto;
  display: grid;
  max-width: 1200px;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 3fr 9fr;
  grid-gap: 1vw;
  grid-template-areas:
  "header header"
  "nav content";
}

.app_wrapper_content{
  grid-area: content;
  background: azure;

}

